<template>
  <div class="cockpit-page">
    <div class="utilization-rate">
      <div class="item-Utilization">
        <div class="data-Utilization">
          <div id="myChartOne" :style="{width: '100px', height: '100px'}"></div>
        </div>
        <h2>CPU利用率</h2>
        <p>2020.05.28 10:20:30</p>
      </div>
      <div class="item-Utilization">
        <div class="data-Utilization">
          <div id="myChartTwo" :style="{width: '100px', height: '100px'}"></div>
        </div>
        <h2>内存利用率</h2>
        <p>2020.05.28 10:20:30</p>
      </div>
      <div class="item-Utilization">
        <div class="data-Utilization">
          <div id="myChartThree" :style="{width: '100px', height: '100px'}"></div>
        </div>
        <h2>物理机磁盘利用率</h2>
        <p>2020.05.28 10:20:30</p>
      </div>
    </div>
    <div class="total-exhibition">
      <div class="tatal-item">
        <div class="item-left">
          <p>合计</p>
          <span>100人</span>
        </div>
        <div class="item-right">
          <p>今日</p>
          <p>累计活跃用户数</p>
          <span>2020.06.28-2020.06.28</span>
        </div>
      </div>
      <div class="tatal-item">
        <div class="item-left">
          <p>合计</p>
          <span>100人</span>
        </div>
        <div class="item-right">
          <p>今日</p>
          <p>累计活跃用户数</p>
          <span>2020.06.28-2020.06.28</span>
        </div>
      </div>
      <div class="tatal-item">
        <div class="item-left">
          <p>合计</p>
          <span>100人</span>
        </div>
        <div class="item-right">
          <p>今日</p>
          <p>累计活跃用户数</p>
          <span>2020.06.28-2020.06.28</span>
        </div>
      </div>
    </div>
    <div class="data-echarts">
      <div class="echarts-header">
        <h2>数据概览</h2>
        <el-date-picker
          v-model="valueTime"
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          @change="changeData"
        ></el-date-picker>
      </div>
      <div class="echarts-content">
        <div class="echarts-item">
          <div class="echarts-time">2020.06.22-2020.06.28</div>
          <div id="myChart" :style="{width: '500px', height: '300px'}"></div>
        </div>
        <div class="echarts-item">
          <div class="echarts-time">2020.06.22-2020.06.28</div>
          <div id="myChart1" :style="{width: '500px', height: '300px'}"></div>
        </div>
        <div class="echarts-item">
          <div class="echarts-time">2020.06.22-2020.06.28</div>
          <div id="myChart2" :style="{width: '450px', height: '300px'}"></div>
        </div>
        <div class="echarts-item">
          <div class="echarts-time">2020.06.22-2020.06.28</div>
          <div id="myChart3" :style="{width: '500px', height: '300px'}"></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import "echarts-liquidfill";
export default {
  data() {
    return {
      valueTime: ""
    };
  },
  mounted() {
    this.ChartsOne();
    this.ChartsTwo();
    this.ChartsThree();
    this.echartsOne();
    this.echartsTwo();
    this.echartsThree();
    this.echartsFour();
  },
  methods: {
    // 查询日期
    changeData() {
      console.log(this.valueTime);
    },
    // 第一个水晶球
    ChartsOne() {
      let myChart = this.$echarts.init(document.getElementById("myChartOne"));
      var data = [0.5];
      myChart.setOption({
        series: [
          {
            type: "liquidFill",
            radius: "80%",
            center: ["50%", "50%"],
            data: data,
            backgroundStyle: {
              color: "#fff"
            },
            outline: {
              borderDistance: 3,
              itemStyle: {
                borderWidth: 1,
                borderColor: "#5ABD8C",
                shadowBlur: 0
              }
            },
            color: ["#5ABD8C"],
            label: {
              normal: {
                formatter: "50%",
                textStyle: {
                  fontSize: 14,
                  color: "#fff",
                  //  fontWeight: 600,
                  textBorderColor: "#5ABD8C",
                  textBorderWidth: 1
                }
              }
            }
          }
        ]
      });
    },
    ChartsTwo() {
      let myChart = this.$echarts.init(document.getElementById("myChartTwo"));
      var data = [0.5];
      myChart.setOption({
        series: [
          {
            type: "liquidFill",
            radius: "80%",
            center: ["50%", "50%"],
            data: data,
            backgroundStyle: {
              color: "#fff"
            },
            outline: {
              borderDistance: 3,
              itemStyle: {
                borderWidth: 1,
                borderColor: "#5ABD8C",
                shadowBlur: 0
              }
            },
            color: ["#5ABD8C"],
            label: {
              normal: {
                formatter: "50%",
                textStyle: {
                  fontSize: 14,
                  color: "#fff",
                  //  fontWeight: 600,
                  textBorderColor: "#5ABD8C",
                  textBorderWidth: 1
                }
              }
            }
          }
        ]
      });
    },
    ChartsThree() {
      let myChart = this.$echarts.init(document.getElementById("myChartThree"));
      var data = [0.5];
      myChart.setOption({
        series: [
          {
            type: "liquidFill",
            radius: "80%",
            center: ["50%", "50%"],
            data: data,
            backgroundStyle: {
              color: "#fff"
            },
            outline: {
              borderDistance: 3,
              itemStyle: {
                borderWidth: 1,
                borderColor: "#5ABD8C",
                shadowBlur: 0
              }
            },
            color: ["#5ABD8C"],
            label: {
              normal: {
                formatter: "50%",
                textStyle: {
                  fontSize: 14,
                  color: "#fff",
                  //  fontWeight: 600,
                  textBorderColor: "#5ABD8C",
                  textBorderWidth: 1
                }
              }
            }
          }
        ]
      });
    },
    // 第一个图表
    echartsOne() {
      let myChart = this.$echarts.init(document.getElementById("myChart"));
      myChart.setOption({
        title: {
          text: "系统访问量"
        },
        xAxis: {
          type: "category",
          //字体倾斜
          axisLabel: {
            interval: 0,
            rotate: 40,
            textStyle: {
              color: "#343434"
            },
            fontSize: "8"
          },
          axisTick: {
            //坐标轴刻度相关设置。
            show: false
          },
          axisLine: {
            show: false
          },
          data: [
            "信息安全与支撑平台",
            "信息资源共享交换",
            "共享网站",
            "数据开放网站",
            "心库管理平台",
            "基础数据库",
            "运行管理平台",
            "审计日志系统",
            "大数据平台"
          ]
        },
        yAxis: {
          name: "单位（次）",
          type: "value",
          splitNumber: 4, // X轴计算倍数
          offset: 20, //文字距离刻度周距离
          axisLine: {
            //y轴
            show: false
          },
          axisTick: {
            //y轴刻度线
            show: false
          },
          splitLine: {
            //网格线
            show: false
          }
        },
        series: [
          {
            itemStyle: {
              normal: {
                color: "#5ABD8C", //改变折线点的颜色
                lineStyle: {
                  color: "#5ABD8C" //改变折线颜色
                }
              }
            },
            // 渲染数据
            data: [9, 5, 9, 26, 28, 78, 175, 192, 18],
            type: "line"
          }
        ]
      });
    },
    echartsTwo() {
      let myChart = this.$echarts.init(document.getElementById("myChart1"));
      myChart.setOption({
        title: {
          text: "系统在线人数"
        },
        xAxis: {
          type: "category",
          //字体倾斜
          axisLabel: {
            textStyle: {
              color: "#343434"
            },
            fontSize: "12"
          },
          axisTick: {
            //坐标轴刻度相关设置。
            show: false
          },
          axisLine: {
            show: false
          },
          data: ["06-22", "06-23", "06-24", "06-25", "06-26", "06-27"]
        },
        yAxis: {
          name: "单位（人）",
          type: "value",
          splitNumber: 4, // X轴计算倍数
          offset: 20, //文字距离刻度周距离
          axisLine: {
            //y轴
            show: false
          },
          axisTick: {
            //y轴刻度线
            show: false
          },
          splitLine: {
            //网格线
            show: false
          }
        },
        series: [
          {
            itemStyle: {
              normal: {
                color: "#5ABD8C", //改变折线点的颜色
                lineStyle: {
                  color: "#5ABD8C" //改变折线颜色
                }
              }
            },
            // 渲染数据
            data: [10, 50, 220, 340, 330, 220],
            type: "line"
          }
        ]
      });
    },
    echartsThree() {
      let myChart = this.$echarts.init(document.getElementById("myChart2"));
      myChart.setOption({
        title: {
          text: "待办数量排名（TOP10）"
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow"
          }
        },
        xAxis: {
          name: "(数量)",
          type: "value",
          splitNumber: 5, // X轴计算倍数
          axisLabel: {
            textStyle: {
              color: "#343434"
            },
            fontSize: "10"
          },
          axisTick: {
            //坐标轴刻度相关设置。
            show: false
          },
          splitLine: {
            //网格线
            show: false
          },
          axisLine: {
            show: false
          }
        },
        yAxis: {
          type: "category",
          // offset: 3, //文字距离刻度周距离
          axisLine: {
            //y轴
            show: false
          },
          axisTick: {
            //y轴刻度线
            show: false
          },
          splitLine: {
            //网格线
            show: false
          },
          data: [
            "张丽",
            "刘军",
            "帅飞",
            "周董",
            "张三",
            "王五",
            "王心凌",
            "周晓琳",
            "李亮"
          ]
        },
        series: [
          {
            type: "bar",
            barWidth: 8, //柱图宽度
            itemStyle: {
              // 柱状图颜色
              normal: {
                color: "#5ABD8C",
                label: {
                  show: true,
                  position: "right",
                  textStyle: {
                    color: "#343434"
                  }
                }
              }
            },
            data: [110, 210, 900, 1000, 1200, 1658, 2300, 2800, 4680]
          }
        ]
      });
    },
    echartsFour() {
      let myChart = this.$echarts.init(document.getElementById("myChart3"));
      myChart.setOption({
        title: {
          text: "待阅任务完成情况"
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            crossStyle: {
              color: "#999"
            }
          }
        },
        legend: {
          orient: "horizontal",
          x: "center",
          y: "275px",
          width: "300",
          itemWidth: 10,
          itemHeight: 10,
          textStyle: {
            color: "#343434"
          },
          data: ["已阅任务", "待阅任务"]
        },
        xAxis: [
          {
            type: "category",
            axisLabel: {
              show: true,
              textStyle: {
                color: "#343434", //更改坐标轴文字颜色
                fontSize: 10 //更改坐标轴文字大小
              }
            },
            axisLine: {
              //y轴
              show: false
            },
            axisTick: {
              //y轴刻度线
              show: false
            },
            splitLine: {
              //网格线
              show: false
            },
            data: ["06-22", "06-23", "06-24", "06-25", "06-26", "06-27"],
            axisPointer: {
              type: "shadow"
            }
          }
        ],
        yAxis: [
          {
            type: "value",
            axisLine: {
              //y轴
              show: false
            },
            axisTick: {
              //y轴刻度线
              show: false
            },
            splitLine: {
              //网格线
              show: false
            }
          }
        ],
        series: [
          {
            name: "已阅任务",
            type: "bar",
            barWidth: 15, //柱图宽度
            itemStyle: {
              // 柱状图颜色
              normal: {
                color: "#5ABD8C",
                label: {
                  show: true,
                  position: "top",
                  textStyle: {
                    color: "#343434"
                  }
                }
              }
            },
            data: [
              2.0,
              4.9,
              7.0,
              23.2,
              25.6,
              76.7,
              135.6,
              162.2,
              32.6,
              20.0,
              6.4,
              3.3
            ],
            barGap: 0
          },
          {
            name: "待阅任务",
            type: "bar",
            barWidth: 15, //柱图宽度
            itemStyle: {
              // 柱状图颜色
              normal: {
                color: "#15253A",
                label: {
                  show: true,
                  position: "top",
                  textStyle: {
                    color: "##343434"
                  }
                }
              }
            },
            data: [
              2.6,
              5.9,
              9.0,
              26.4,
              28.7,
              70.7,
              175.6,
              182.2,
              48.7,
              18.8,
              6.0,
              2.3
            ],
            barGap: 0
          }
        ]
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.cockpit-page {
  p,
  h2 {
    margin: 0;
  }
  padding: 10px;
  .utilization-rate {
    display: flex;
    justify-content: space-around;
    background-color: #fff;
    .item-Utilization {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 20px 0;
      box-sizing: border-box;
      // .data-Utilization {
      //   width: 50px;
      //   height: 50px;
      //   border: 1px solid #5abd8c;
      //   border-radius: 50%;
      // }
    }
  }
  .total-exhibition {
    margin-top: 10px;
    display: flex;
    .tatal-item {
      flex: 1;
      display: flex;
      justify-content: space-between;
      align-items: center;
      background: #fff;
      margin-right: 10px;
      padding: 10px;
      box-sizing: border-box;
      &:nth-child(3) {
        margin: 0;
      }
      .item-left {
        font-size: 17px;
        span {
          color: #5abd8c;
        }
      }
      .item-right {
        margin-right: 30px;
        p {
          color: #343434;
          font-size: 17px;
        }
        span {
          font-size: 12px;
          color: #555555;
        }
      }
    }
  }
  .data-echarts {
    background: #fff;
    margin-top: 10px;
    .echarts-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 10px;
      box-sizing: border-box;
      border-bottom: 1px solid #d5d5d5;
      h2 {
        font-size: 16px;
      }
    }
    .echarts-content {
      display: flex;
      flex-wrap: wrap;
      padding: 15px;
      box-sizing: border-box;
      .echarts-item {
        width: 49%;
        border: 1px solid #d5d5d5;
        padding: 20px;
        box-sizing: border-box;
        overflow: hidden;
        position: relative;
        margin-bottom: 15px;
        &:nth-child(1),
        &:nth-child(3) {
          margin-right: 15px;
        }
        .echarts-time {
          position: absolute;
          right: 20px;
        }
      }
    }
  }
}
</style>
<style>
.el-date-editor--daterange.el-input,
.el-date-editor--daterange.el-input__inner,
.el-date-editor--timerange.el-input,
.el-date-editor--timerange.el-input__inner {
  width: 266px;
}
.el-input__inner {
  height: 30px;
}
.el-date-editor .el-range__icon {
  line-height: 24px;
}
.el-date-editor .el-range-separator {
  line-height: 22px;
}
</style>


